<template>
  <div class="wsh-btn-wrapper">
    <button :style="btnStyle" class="wsh-btn" @click.prevent="btnClick" ref="button">
      <slot text>{{ text }}</slot>
    </button>
    <transition name="fade">
      <div class="comfirm" v-if="comfirmVisible" :style="comfirmStyle">
        <div class="comfirm-inner" :style="{ 'border-color': detemineColor(type) }">
          <div class="comfirm-body">{{ comfirmText?comfirmText:`确认${text}?` }}</div>
          <div class="comfirm-btn">
            <button class="cancel" @click.prevent="handleClick('cancel')">取消</button>
            <button class="OK" style="color: red" @click.prevent="handleClick('comfirm')">
              确认
            </button>
          </div>
        </div>
        <div class="icon" :style="{ color: detemineColor(type) }">▼</div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'wsh-button',
  props: {
    type: {
      type: String,
      default: () => 'primary',
    },
    fontColor: {
      type: String,
      default: () => '#fff',
    },
    comfirmText: {
      type: String,
      default: () => null,
    },
    text: {
      type: String,
      default: () => '按钮',
    },
    showComfirm: {
      type: Boolean,
      default: () => false,
    },
  },
  emits: ['comfirm', 'cancel'],
  data() {
    return {
      comfirmVisible: false,
    };
  },
  computed: {
    btnStyle() {
      return {
        'background-color': this.detemineColor(this.type),
        padding: '0.1rem .5rem',
        'border-radius': '0.1rem',
        color: this.fontColor,
        cursor: 'pointer',
        border: 'none',
      };
    },
    comfirmStyle() {
      const btnWidth = this.$refs['button'].clientWidth;
      const width = btnWidth * 2;
      return {
        width: width + 'px',
        top: '-120px',
        left: -(width - btnWidth) / 2 + 'px',
      };
    },
  },
  methods: {
    btnClick() {
      if (this.showComfirm) {
        this.comfirmVisible = true;
      } else this.$emit('comfirm');
    },
    detemineColor(type) {
      let typeMap = {
        success: '#7ABC65',
        danger: '#E2643D',
        info: '#2BCEEC',
        waring: '#FDD635',
        primary: 'rgba(100, 100, 100, 0.5)',
      };
      return typeMap[type];
    },
    handleClick(type) {
      this.$emit(type);
      this.comfirmVisible = false;
    },
  },
};
</script>

<style scoped lang="scss">
@include fade-transition();

.wsh-btn-wrapper {
  display: inline-block;
  position: relative;
  .wsh-btn {
    &:active {
      background: blue;
    }
  }
  .comfirm {
    position: absolute;
    .icon {
      position: absolute;
      bottom: -15px;
      left: 50%;
      color: $--baseColor;
    }
    .comfirm-inner {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      gap: 5px;
      width: 100%;
      padding: 1vw;
      border-radius: 0.5vw;
      background-color: rgb(255, 255, 255);
      border: 1px solid $--baseColor;
      z-index: 999;
    }
    .comfirm-body {
      flex: 1;
      width: 100%;
      min-height: 3vw;
    }
    .comfirm-btn {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 1vw;
      button {
        flex: 1;
        cursor: pointer;
        &.comfirm {
          color: 'red';
        }
      }
    }
  }
}
</style>
