import { createRouter, createWebHashHistory } from 'vue-router';
import nprogress from 'nprogress';
import { store } from '@/store/index.js';

const tokenStore = store;

export const routes = [
  {
    path: '/login/',
    name: 'login',
    component: () => import('@/pages/login/index.vue'),
    meta: { title: '登录', loged: false },
  },
  {
    path: '/',
    name: 'index',
    component: () => import('@/pages/dashboard/index.vue'),
    meta: {
      title: 'Mylog管理系统',
      keepAlive: true,
      loged: process.env.NODE_ENV === 'development' ? true : false,
    },
    redirect: 'overview',
    children: [
      {
        path: '/overview',
        name: 'overview',
        redirect: '/overview/stat',
        component: () => import('@/pages/dashboard/overview/index.vue'),
        meta: { nav: true, title: '总览', keepAlive: true, loged: true },
        children: [
          {
            path: '/overview/stat',
            name: 'stat',
            component: () => import('@/pages/dashboard/overview/stat/index.vue'),
            meta: { nav: true, title: '访问统计', keepAlive: true, loged: true },
          },
          {
            path: '/overview/today',
            name: 'today',
            component: () => import('@/pages/dashboard/overview/today/index.vue'),
            meta: { nav: true, title: '匿名记录', keepAlive: true, loged: true },
          },
          {
            path: '/overview/ppv_today',
            name: 'ppv_today',
            component: () => import('@/pages/dashboard/overview/page-view/index.vue'),
            meta: { nav: true, title: '具名记录', keepAlive: true, loged: true },
          },
          {
            path: '/overview/distribution',
            name: 'distribution',
            component: () => import('@/pages/dashboard/overview/distribution/index.vue'),
            meta: { nav: true, title: '地理分布', keepAlive: true, loged: true },
          },
        ],
      },
      {
        path: '/article',
        name: 'article',
        redirect: '/article/blogs',
        component: () => import('@/pages/dashboard/article/index.vue'),
        meta: { nav: true, title: '文章', keepAlive: true, loged: true },
        children: [
          {
            path: '/article/blogs',
            name: 'blogs',
            component: () => import('@/pages/dashboard/article/blogs/index.vue'),
            meta: { nav: true, title: '博文', keepAlive: true, loged: true },
          },
          {
            path: '/article/drafts',
            name: 'drafts',
            component: () => import('@/pages/dashboard/article/drafts/index.vue'),
            meta: { nav: true, title: '草稿', keepAlive: true, loged: true },
          },
          {
            path: '/article/writting/:id?/:type?',
            name: 'writting',
            component: () => import('@/pages/dashboard/article/writting/index.vue'),
            meta: { nav: true, title: '写作', keepAlive: true, loged: true },
          },
        ],
      },

      {
        path: '/comments',
        name: 'comments',
        component: () => import('@/pages/dashboard/comments/index.vue'),
        meta: { nav: true, title: '评论', keepAlive: true, loged: true },
      },

      {
        path: '/moment',
        name: 'moment',
        redirect: '/moment/moment-list',
        component: () => import('@/pages/dashboard/moment/index.vue'),
        meta: { nav: true, title: '时刻', keepAlive: true, loged: true },
        children: [
          {
            path: '/moment/moment-list',
            name: 'moment-list',
            component: () => import('@/pages/dashboard/moment/MomentList.vue'),
            meta: { nav: true, title: '列表', keepAlive: true, loged: true },
          },
          {
            path: '/moment/moment-add',
            name: 'moment-add',
            component: () => import('@/pages/dashboard/moment/MomentAdd.vue'),
            meta: { nav: true, title: '新增', keepAlive: true, loged: true },
          },
        ],
      },
      {
        path: '/user',
        name: 'user',
        component: () => import('@/pages/dashboard/user/index.vue'),
        meta: { nav: true, title: '用户', keepAlive: true, loged: true },
      },
    ],
  },
];
const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

// 全局路由守卫
router.beforeEach((to, from, next) => {
  nprogress.start();
  document.title = to.meta.title || 'Mylog';

  document.title = `Mylog-${to.meta.title}`;
  if (to.meta.loged) {
    if (tokenStore.getToken()) next();
    else next(`/login?redirect=${to.path}`);
  } else next();
});

router.afterEach((to, from, next) => {
  const navs = to.matched.map(item => item.meta.title);
  store.setNav(navs.join(' >> ') || '');
  nprogress.done();
});

export default router;
