<template>
  <div class="custom-table">
    <div class="custom-table-wrapper">
      <el-table :data="datas" setCurrentRow stripe style="width: 100%" @row-click="selectRow">
        <template v-for="(item, i) in props" :key="i">
          <el-table-column
            v-if="item.width"
            :prop="item.name"
            :label="item.label"
            :width="item.width"
          />
          <el-table-column v-else :prop="item.name" :label="item.label" />
        </template>
      </el-table>
    </div>

    <div class="pagination">
      <el-pagination
        v-show="total !== 0"
        @current-change="pageChange"
        small
        layout="prev, pager, next"
        :total="total"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: 'CustomTable',
  props: {
    datas: {
      type: Array,
      default: () => [],
    },
    props: {
      type: Array,
      default: () => [],
    },
    total: {
      type: Number,
      default: () => 1,
    },
    filter:{
      type: Boolean,
      default: ()=> false,
    }
  },
  emits: ['select', 'page-change'],
  computed:{
    filters(){
      return ""
    }
  },
  methods: {
    selectRow(row) {
      this.$emit('select', row);
    },
    pageChange(val) {
      this.$emit('page-change', val - 1);
    },
  },
};
</script>

