<template>
  <div class="wsh-collapse">
    <Collapse :navs="navs" @select-change="navigateTo" />
  </div>
</template>

<script>
import Collapse from '@/components/collapse/index.vue';
import {routes} from '@/router'
import {routes2nav} from '@/utils/tool'

let navs = routes2nav(routes)

export default {
  name: 'NavigationBar',
  components: { Collapse },
  emits: ['route-change'],
  computed:{
    navs:() => navs
  },
  created(){
    console.log(routes2nav(routes))
  },
  mounted() {
    this.active = this.$route.name;
  },
  methods: {
    navigateTo(navOpt) {
      this.$router.push({ name: navOpt.name });
      this.$emit('route-change', navOpt);
    },
  },
};
</script>

