<template>
  <div class="layout">
    <div class="nav-wrapper">
      <NavigationBar />
    </div>
    <div class="dashboard">
      <div class="header">
        <Header />
      </div>
      <div class="dashboard-content">
        <div class="content-inner">
          <router-view v-slot="{ Component }">
            <keep-alive v-if="$route.meta.keepAlive">
              <Transition name="fade" >
                <component :is="Component" :key="$route.name"  />
              </Transition>
            </keep-alive>
            <Transition name="fade" v-else>
              <component :is="Component" :key="$route.name" />
            </Transition>
          </router-view>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getToken } from '@/utils/cookieJar';
import { routes } from '@/router';

export default {
  name: 'Dashboard',
  components: {},
  data() {
    return {
      token: '',
      active: 'stat',
      navs: [],
    };
  },
  beforeMount() {
    
    let pages = routes.find(item => item.name == 'index');
    if (pages) {
      pages.children.forEach(p => {
        this.navs.push({ name: p.name, title: p.meta.title });
      });
    }
  },
  mounted() {
    this.token = getToken();
  },
};
</script>

