// 自动注册公共组件目录下所有组件
// 需要在main文件中引入
const importFn = require.context("./", false, /\.vue$/);
// console.dir(importFn.keys());
export default {
  install(app) {
    importFn.keys().forEach((key) => {
      const component = importFn(key).default;
      app.component(component.name, component);
    });
  },
};
