<template>
  <div class="refresh-icon" :style="iconStyle" title="更新" @click="$emit('refresh')">
    <SvgIcon iconClass="refresh" />
  </div>
</template>

<script>
export default {
  name: 'Refresh',
  emits: ['refresh'],
  props: {
    position: {
      type: Array,
      default: () => ['50vh', '1vw'],
    },
  },
  computed: {
    iconStyle() {
      return {
        position: 'fixed',
        bottom: this.position[0],
        right: this.position[1],
      };
    },
  },
};
</script>

<style scoped lang="scss">
.refresh-icon {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  color: #fff;
  background: $--baseColor;
  cursor: pointer;
  z-index: 999;
  &:hover {
    transform: scale(1.2);
  }
}
</style>
