<template>
  <div class="wsh-collapse-item">
    <div
      :class="['wsh-collapse-item-header', expand == true ? 'expand' : '']"
      @click="expandAvailable"
    >
      <span class="title">{{ prop.title }}</span>
      <span class="icon" :style="IconStyle">{{iconAvailable?'⌵':'&nbsp;&nbsp;'}}</span>
    </div>
    <div class="wsh-collapse-item-body fade" :style="bodyStyle">
      <div
        class="sub-item"
        v-for="(subProp, j) in prop.children"
        :key="j"
        @click.prevent="$emit('select', prop, subProp, expand)"
      >
        <span>{{ subProp.title }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'collapse-item',
  emits: ['select', 'expand-change'],
  expose: ['setExpand'],
  props: {
    prop: {
      type: Object,
      rquire: true,
    },
  },
  data() {
    return {
      expand: false,
    };
  },
  computed: {
    bodyStyle() {
      return {
        width: '100%',
        height: this.expand ? this.subItemCount * 40 + 'px' : '0px',
      };
    },
    IconStyle() {
      return {
        transform: this.expand == true ? 'rotate(0deg)' : 'rotate(90deg)',
        'font-weight': 'bolder',
      };
    },
    subItemCount() {
      if (!this.prop.children) return 0;
      if (this.prop.children.length > 0) return this.prop.children.length;
      else return 0;
    },
    iconAvailable() {
      if (this.prop.children) {
        if (this.prop.children.length > 0) return true;
        else return false;
      } else return false;
    },
  },
  methods: {
    expandAvailable() {
      this.$emit('expand-change', this.prop.name, this.expand);
      if (this.iconAvailable) this.expand = !this.expand;
      else {
        this.$emit('select', this.prop, null, this.expand);
      }
    },
    setExpand(enable) {
      this.expand = enable;
    },
  },
};
</script>

<style scoped lang="scss">
.wsh-collapse-item {
  cursor: pointer;
  background-color: #1e7ea4;

  .wsh-collapse-item-header {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    width: 100%;
    height: 60px;
    font-weight: bold;
    border: 1px solid rgba(205, 202, 202, 0.433);
    border-left: none;
    border-right: none;
    .title{
      text-align: center;
    }
    .icon{
    }
    &.expand {
      background-color: #2688af;
    }
  }
  .wsh-collapse-item-body {
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: hidden;
    background-color: rgba(205, 202, 202, 0.213);
    transition: height 0.5s;
    .sub-item {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 60px;
      background-color: #2688af;
      border-bottom: 1px solid rgba(128, 128, 128, 0.292);
      &:hover {
        background-color: #58b0d3;
      }
    }
  }
}
</style>
