import { defineStore } from 'pinia';
import { getToken, setToken, removeToken } from '@/utils/cookieJar';
import { pinia } from './pinia';

export const createStore = defineStore('publicStore', {
  state: () => ({ token: null, nav: 'Mylog管理系统' }),
  getters: {
    navigation() {
      return this.nav;
    },
  },
  actions: {
    setToken(token) {
      this.token = token;
      setToken(token);
    },
    getToken() {
      const tk = getToken();
      if (!this.token) {
        this.token = tk;
      }
      return this.token;
    },
    removeToken() {
      this.token = null;
      removeToken();
    },
    setNav(text){
      this.nav = text
    }
  },
});
export const store = createStore(pinia);
