import request from "../utils/request";
export function momnetDelete({id}){
  return request({
    url: '/moment/delete/' + id,
    method: 'get',
  })
}

export function momnetUpdate(data){
  return request({
    url: '/moment/update',
    method: 'post',
    data
  })
}

export function OneCover(data){
  return request({
    url: '/moment/cover',
    method: 'post',
    data: data,
    
  })
}
export function momnetCreate(data){
  return request({
    url: '/moment/create',
    method: 'post',
    data,
  })
}