<template>
  <div class="cmt">
    <div class="create" style="width: 100%">
      <input class="nickname" type="text" v-model="nickName" placeholder="昵称" />
      <input
        class="cmt-item body"
        v-model="body"
        name="cmt"
        type="text"
        placeholder="在此输入文字"
      />
      <span class="cmt-item" @click="createCmt">>></span>
    </div>
    <div class="cmts">
      <div class="cmts-item" v-for="(item, index) in comments">
        <span class="nickname">{{ item.nickName }}：</span>
        <span class="body">{{ item.body }}</span>
        <span class="date">{{ parseDate(item.time) }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { commentCreate, commentGetAll } from "@/apis/index.js";
import { translateDate } from "@/utils/tool";

export default {
  name: "Comment",
  props: {
    id: {
      type: Number,
      require: true,
      default: undefined,
    },
  },
  components: {},
  data() {
    return {
      body: null,
      nickName: null,
      //   评论列表
      comments: [],
    };
  },
  unmounted() {},
  methods: {
    parseDate(date) {
      return translateDate(date);
    },
    createCmt() {
      if (this.body !== null && this.nickName !== null) {
        commentCreate({
          blogId: this.id,
          nickName: this.nickName,
          body: this.body,
        }).then((res) => {
          if (res.data.status) {
            this.nickName = "";
            this.body = "";
            this.refreshCmt(this.id);
          }
        });
      }
    },
    refreshCmt(id) {
      commentGetAll({ id: id }).then((res) => {
        this.comments.length = 0;
        this.comments.push(...res.data.data);
      });
    },
  },
  watch: {
    id(val) {
      if (!val) return;
      this.refreshCmt(val);
    },
    nickName(val) {
      if (val.length >= 12) {
        alert("昵称必须小于12字符");
        this.nickName = "";
      }
    },
    body(val) {
      if (val.length >= 120) {
        alert("内容字数小于120字符");
        this.body = "";
      }
    },
  },
};
</script>

