<template>
  <div class="wsh-collapse">
    <CollapseItem
      v-for="(nav, i) in navs"
      :key="i"
      :prop="nav"
      @select="selectChange"
      @expand-change="expandChange"
      :ref="nav.name"
    />
  </div>
</template>

<script>
import CollapseItem from './CollapseItem.vue';
export default {
  name: 'collapse',
  components: { CollapseItem },
  emits: ['select-change'],
  props: {
    navs: {
      type: Array,
      default: () => [],
    },
  },
  mounted() {
    this.activeExpand(this.navs[0].name);
  },
  methods: {
    expandChange(name, expand) {
      this.collapseExcept(name);
    },
    selectChange(nav, subNav) {
      this.$emit('select-change', subNav ? subNav : nav);
    },
    collapseExcept(name) {
      for (let k in this.$refs) {
        if(k != name) this.$refs[k][0].setExpand(false);
      }
    },
    activeExpand(name) {
      this.$refs[name][0].setExpand(true);
    },
  },
};
</script>

